/* ==========================================================================
   Product List - Carousel shortcode styles - begin
   ========================================================================== */

.mkdf-plc-holder {
    @include mkdfRelativeHolderLayout();
     margin-top: 40px;

	&.mkdf-simple-layout{
		.mkdf-plc-item:hover{

			.mkdf-plc-image-outer {

				.mkdf-plc-image img {
					@include mkdfTransform(scale(1.03) rotate(0.01deg));
				}
			}
		}
	}

    .mkdf-plc-main-title {
    	margin: 0;
    	display: inline-block;
    }

    .mkdf-plc-outer {
        @include mkdfRelativeHolderLayout();
        margin-top: 17px;
    }

    .mkdf-plc-item {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        padding: 0;
        z-index: 100;
        overflow: hidden;
        box-sizing: border-box;

        &:hover {

            .mkdf-plc-image-outer {
                .mkdf-plc-text {
                    opacity: 1;
                }
            }
        }

        .touch & {
            cursor: pointer;
        }

        .mkdf-plc-image-outer {
            @include mkdfRelativeHolderLayout();

            .mkdf-plc-image {
                @include mkdfRelativeHolderLayout();
				overflow: hidden;

				img {
					@include mkdfTransform(scale(1) rotate(0.01deg));
					@include mkdfTransitionTransform($default-transition-duration $default-easing-function);
				}

                .mkdf-plc-onsale,
                .mkdf-plc-out-of-stock,
                .mkdf-pli-new-product, 
                .mkdf-plc-new-product {
	                @include mkdfWooCommerceMarksGeneralStyle();
                }

                .mkdf-plc-onsale {
	                @include mkdfWooCommerceSaleStyle();
                }

                .mkdf-plc-out-of-stock {
	                @include mkdfWooCommerceOutOfStockStyle();
                }
	
	            .mkdf-plc-new-product {
		            @include mkdfWooCommerceNewProductStyle();
	            }
            }

            .mkdf-plc-text {
                position: absolute;
                top: 0;
                left: 0;
                display: inline-block;
                width: 100%;
                height: 100%;
                padding: 10px;
                opacity: 0;
                background-color: rgba($default-background-color, 0.9);
                box-sizing: border-box;
                @include mkdfTransition(opacity .2s ease-out, background-color .2s ease-out);
            }

            .mkdf-plc-text-outer {
                position: relative;
                display: table;
                width: 100%;
                height: 100%;
                text-align: center;
            }

            .mkdf-plc-text-inner {
                position: relative;
                display: table-cell;
                width: 100%;
                height: 100%;
                vertical-align: middle;
            }
        }

        .mkdf-plc-title {
            margin: 0;
        }
	
	    .mkdf-plc-category {
		    margin: 11px 0 3px;
	    }

        .mkdf-plc-excerpt {
            color: $default-heading-color;
            line-height: 1.3em;
            margin: 11px 0 3px;
        }

        .mkdf-plc-rating-holder {
            @include mkdfRelativeHolderLayout();
            line-height: 1;
            margin: 10px 0 3px;
        }

        .mkdf-plc-rating {
	        @include mkdfWooCommerceRatingsStyle();
        }

        .mkdf-plc-price {
	        @include mkdfWooCommercePriceStyle();
	        font-size: 12px;
        }
	
	    

        .mkdf-plc-link {
            @include mkdfAbsoluteHolderLayout();
        }
    }

    &.mkdf-standard-layout {

        .mkdf-plc-item {
    
            .mkdf-plc-title,
            .mkdf-plc-category a,
            .mkdf-plc-excerpt,
            .mkdf-plc-rating,
            .mkdf-plc-price {
                color: #2b2b2b;
            }
	
			.mkdf-plc-price del,
	        .mkdf-plc-category a:hover {
		        color: $first-main-color;
	        }
	        .button,
		    .added_to_cart {
			    @include mkdfButtonDefaultStyle();
			    @include mkdfButtonMediumParams();
			    white-space: nowrap;
			    z-index: 3;
		    }
		
		    .button.added {
			    display: none;
		    }
		
		    .mkdf-plc-add-to-cart {
			    margin: 20px 0 0;
			
			    &.mkdf-default-skin {
				
				    .button,
				    .added_to_cart {
					    @include mkdfButtonSolidColor();
					
					    &:hover {
						    @include mkdfButtonSolidHoverColor();
					    }
				    }
			    }
			
			    &.mkdf-light-skin {
				
				    .button,
				    .added_to_cart {
					    color: $default-heading-color;
					    background-color: #fff;
					
					    &:hover {
						    color: #fff;
						    background-color: $first-main-color;
					    }
				    }
			    }
			
			    &.mkdf-dark-skin {
				
				    .button,
				    .added_to_cart {
					    color: #fff;
					    background-color: $default-heading-color;
					
					    &:hover {
						    color: #fff;
						    background-color: $first-main-color;
					    }
				    }
			    }
				.button{
					&.added{
						display: none;
					}
				}
		    }
        }
    }

    &.mkdf-simple-layout {
    	.mkdf-plc-item {
    		&:hover {
	            .mkdf-plc-text-wrapper {
	            	.mkdf-plc-add-to-cart {
	            		 opacity: 1;
	                	 @include mkdfTransform(translateX(-50%));
	            	}
	            	.mkdf-plc-price {
	            		 opacity: 0;
	                    @include mkdfTransform(translateX(60px));
	            	}
	               
	            }
	        }
    	}
        
        .mkdf-plc-image-outer {
	        position: relative;
		    display: inline-block;
		    width: 100%;
		    vertical-align: middle;
		    overflow: hidden;
		}

        .mkdf-plc-text-wrapper {

            position: relative;
		    display: inline-block;
		    width: 100%;
		    vertical-align: middle;
		    margin: 20px 0 2px;
		    text-align: center;
        }

        .mkdf-plc-text,
        .mkdf-plc-text-outer,
        .mkdf-plc-text-inner {
            @include mkdfRelativeHolderLayout();
            -webkit-transform: translateZ(0);

            .mkdf-plc-price {
		        @include mkdfWooCommercePriceStyle();
		        @include mkdfTransition(all .3s ease);
		        display: inline-block;
		        font-size: 12px;
        	}

        	.mkdf-plc-add-to-cart{
				display: inline-block;
				position: absolute;
	            left: 50%;
	            opacity: 0;
	            font-size: 12px;
	            @include mkdfTransform(translateX(-100%));
	            @include mkdfTransition(all .4s ease);

	            a {
	            	color: #afafaf;
					&:hover {
						color: $first-main-color;
					}
	            }

				.button.added {
					display: none;
				}
			}
		}
		
    }
	
	&.mkdf-plc-nav-light-skin {
		
		.owl-nav {
			
			.owl-prev,
			.owl-next {
				color: $header-light-color;
				
				&:hover {
					color: $header-light-hover-color;
				}
			}
		}
	}
	&.mkdf-plc-nav-above-slider {
		
		.owl-prev {
			position: absolute;
			left: auto;
			right: 10px;
			top: -30px;
			> span {
				&:before {
					font-family: 'ElegantIcons';
					content: "\44";
					font-size: 24px;
				}
			}

			svg{
				display: none;
			}
		}
		.owl-next {
			position: absolute;
			top: -30px;
			right: -8px;
			> span {
				&:before {
					font-family: 'ElegantIcons';
					content: "\45";
					font-size: 24px;
				}
			}
			svg{
				display: none;
			}
		}

	}
	
	&.mkdf-plc-pag-inside-slider {
		
		.owl-prev,
		.owl-next {
			@include mkdfTransform(translateY(-50%));
		}
		
		.owl-dots {
			position: absolute;
			left: 0;
			bottom: 10px;
			margin: 0;
		}
	}
	
	&.mkdf-plc-pag-light-skin {
		
		.owl-dots {
			
			.owl-dot {

				span {
					background-color: $header-light-hover-color;
				}

				&:hover,
				&.active {

					span {
						background-color: $header-light-color;
					}
				}
			}
		}
	}
}
/* ==========================================================================
   Product List - Carousel shortcode styles - end
   ========================================================================== */