/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 0;
    width: auto;
    height: 30px;
    background: $first-main-color;
    color: #fff;
    font-family: $default-heading-font;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 1px;
    text-align: center;
    box-sizing: border-box;
    margin: 10px 0 0 12px;
    padding: 5px 13px;
    z-index: 100;
    @include mkdfTransform(translateZ(0));
}

@mixin mkdfWooCommerceSaleStyle() {
    left: 0;
    color: $default-heading-color;

    background: #fff;
    border-color: transparent $first-main-color transparent transparent;

}

@mixin mkdfWooCommerceOutOfStockStyle() {
    left: 0;
    color: #fff;
    background-color: $default-heading-color;
	border-color: transparent $default-heading-color transparent transparent;
}

@mixin mkdfWooCommerceNewProductStyle() {
    left: 0;

	&:before{
		border-color: transparent $first-main-color transparent transparent;
	}
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommercePriceStyle() {
    position: relative;
    display: block;
    margin: 4px 0 0;
    line-height: 1;
    color: #afafaf;
    font-family: $default-nav-font;
    
    * {
        font-size: inherit;
        line-height: inherit;
   
    }
    
    del {
        margin: 0 4px 0 0;
        color: #afafaf;
    }
    
    ins {
        text-decoration: none;
        color: #2d2b2b;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "ElegantIcons";
    font-size: 12px;
    line-height: 1;
    letter-spacing: 2px;
    color: #ffd117;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin mkdfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\e031\e031\e031\e031\e031";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        
        &:before {
            content: "\e033\e033\e033\e033\e033";
        }
    }
}

@mixin mkdfWooCommerceSingleProductRatingsStyle() {
    
    &:before {
        content: "\e031";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
        z-index: -1;
    }
    
    &.active:after {
        content: "\e033\e033\e033\e033\e033";
        position: absolute;
        right: 0;
        bottom: 7px;
        display: block;
        @extend %star-ratings;
    }
}

%input-style {
    width: 100%;
    margin: 0 0 20px;
    padding: 8px 18px;
    font-family: inherit;
    font-size: 10px;
    line-height: 20px;
    font-weight: inherit;
    color: $default-form-color;
    background-color: transparent;
    border: 1px solid $default-border-color;
    border-radius: 0;
    outline: 0;
    -webkit-appearance: none;
    cursor: pointer;
    box-sizing: border-box;
    @include mkdfTransition(border-color 0.2s ease-in-out);

    &:focus {
        color: $default-heading-color;
        border-color: $default-text-color;
    }

    @include mkdfPlaceholder {
        color: inherit;
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */