/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.mkdf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    padding: 0 10px;

    .touch & {
        cursor: pointer;
    }

    .mkdf-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .mkdf-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;
        color: #1b2024;

        .mkdf-cart-icon-number-holder{
            position: relative;
            display: inline-block;
            vertical-align: middle;
        }

        .mkdf-cart-icon {
            position: relative;
            display: inline-block;
            font-size: 21px;
            vertical-align: top;
            padding-right: 14px;
            
        }
        .mkdf-cart-number {
            position: absolute;
            top: -1px;
            left: 11px;
            width: 13px;
            height: 13px;
            background-color: $first-main-color;
            font-size: 8px;
            color: #fff;
            font-weight: 600;
            line-height: 1.5em;
            border-radius: 50%;
            text-align: center;
           
        }
        .mkdf-cart-icon-text,
        .mkdf-cart-info {
            position: relative;
			color: #1b1b1b;
            font-size: 12px;
			font-weight: 500;
            letter-spacing: .1em;
			line-height: 27px;
        }

        &.mkdf-header-cart-icon-pack {
            .mkdf-cart-icon {
                font-size: 21px;
                line-height: 1em;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        &.mkdf-header-cart-svg-path {
            .mkdf-cart-icon {

                >* {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }
    }

    &:hover {

		.mkdf-shopping-cart-dropdown {
			visibility: visible;
			max-height: 100vh;
			opacity: 1;
			@include mkdfTransition(max-height 0.6s cubic-bezier(.53,.01,.18,1), opacity 0s ease);


		}
    }
}

.mkdf-dark-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.mkdf-light-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.mkdf-shopping-cart-dropdown {
    position: absolute;
    top: 100%;
    left: auto;
    right: 0;
    width: 245px;
    background-color: #fff;
    border: 1px solid $default-border-color;
    text-align: left;
    z-index: 1000;
	max-height: 0;
    opacity: 0;
    visibility: hidden;
	overflow: hidden;
    box-sizing: border-box;
	@include mkdfTransition(opacity 0.2s, max-height 0s linear 0.15s);

    ul {
        margin: 23px 20px;
        padding: 0;

        li {
            @include mkdfTableLayout();
            padding: 0 0 15px;
            box-sizing: border-box;

            &.mkdf-empty-cart {
				
                border-bottom: 0;
                color: #737373;
                font-size: 13px;
                font-weight: 500;
                font-family: $default-nav-font;
                border-bottom: 0;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .mkdf-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 50px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .mkdf-item-info-holder {
        @include mkdfTableCellLayout();
        vertical-align: top;
        padding: 0px 19px 0;

        .mkdf-product-title {
            display: block;
            margin: 0;
            box-sizing: border-box;
            line-height: 1;
        }

        .remove {
            position: absolute;
            top: 12px;
            right: 0;
	        font-size: 12px;
	        line-height: 1;
            color: $first-main-color;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .mkdf-quantity {
            display: inline-block;
            margin: 3px 0 0;
            font-size: 12px;
            letter-spacing: 0.05em;
            color: $meta-color;
            &:after {
                content: 'X';
                display: inline-block;
                position: relative;
                padding-left: 3px;
                color: $meta-color;
            }
        }

        .amount {
            display: inline-block;
            margin: 0;
            font-size: 12px;
            color: $meta-color;
        }
    }

    .mkdf-cart-bottom {
        display: block;

        .mkdf-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 13px;

            > * {
                display: table-cell;
                vertical-align: top;
                color: $default-heading-color;
                line-height: 20px;
            }
            .mkdf-total{
                font-size: 12px;
                font-weight: 700;
                letter-spacing: 0.05em;
            }

            .mkdf-total-amount {
                text-align: right;
	            font-size: 12px;
                font-weight: 500;
            }
        }
        .mkdf-btn-holder:nth-child(2) {
            margin-right: 6px;
        }
        .mkdf-btn-holder {
            display: inline-block;
            float: left;
            margin-bottom: 20px;

        }
        .mkdf-view-cart {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            height: 40px;
            line-height: 40px;
            padding: 0 18px;
            font-size: 9px;
            font-weight: 500;
	        letter-spacing: 1px;
            color: #fff;
            background-color: $first-main-color;
            text-align: center;
            box-sizing: border-box;
            @include mkdfTransition(color .2s ease-out, background-color .2s ease-out);

            span {
                display: block;
            }

            &:hover {
                color: #fff;
                background-color: $first-main-color-light;
            }
        }
        .mkdf-btn-holder:nth-child(3) a {
           background-color: #000000;
           &:hover {
                background-color: #232323;
           }
        }
    }
}

.mkdf-vertical-menu-area {

    .mkdf-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */