/* ==========================================================================
   WooCommerce Result and Ordering styles - begin
   ========================================================================== */
.mkdf-woocommerce-page {

    .woocommerce-result-count,
    .woocommerce-ordering {
        display: inline;
        width: 50%;
        margin: 0 0 17px;
        float: left;
    }

    .woocommerce-result-count {
        font-size: 12px;
        font-family: $default-nav-font;
        line-height: 44px;
        color: #8b8b8b;
    }

    .woocommerce-ordering {
        text-align: right;
        
        .orderby {
            width: auto;
	        min-width: 250px;
	        margin: 0;
	        text-align: initial;
        }
    }
}
/* ==========================================================================
   WooCommerce Result and Ordering styles - end
   ========================================================================== */