/* ==========================================================================
   WooCommerce Variation styles - begin
   ========================================================================== */

.mkdf-woocommerce-page .mkdf-content {
    
    .variations {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        margin: 0;
        border: none;
        padding: 0;
        width: 76%;
        
        tbody {
            display: block;
            width: 100%;
        }
        
        tr {
            display: block;
            height: 51px;
            line-height: 50px;
            vertical-align: middle;
            border: 1px solid #e8e8e8;
            margin-bottom: 20px;
            padding: 0 20px;
            
            &:last-child {
                margin: 0;
            }
        }
        
        td {
            position: relative;
            display: inline-block;
            font-size: 12px;
            text-transform: none;
            text-align: initial;
            color: $default-form-color;
            padding: 0;
	        
	        &.label {
                font-size: 12px;
                color: #999999;

            }
	        &.value {
		        white-space: nowrap;
                float: right;

                .select2-container--default {


                    .select2-selection--single {

                        color: #999999;
                        font-weight: 400;

                        .select2-selection__rendered {

                            font-weight: 400;
                            letter-spacing: 0;
                        }

                        .select2-selection__arrow {
                            right: -3px;
                        }
                    }
                }
		        
		        select {
			        width: auto;
			        min-width: 180px;
		        }
	        }
        }
        
        .reset_variations {
            display: none !important; // Push selection button to the right side, when reset varition field is visible , that's not possible. 
	        margin: 0 0 0 15px;
            color: $default-heading-color;
        }
        .select2-selection--single {
            border: 1px solid transparent;
        }
        .select2-container--default {
            text-align: left;
            width: auto;
        }
        .select2-dropdown {
            left: -1px;
        }
    }
    
    .single_variation_wrap {
        margin: 17px 0 0;
        
        .single_variation {
            margin: 0 0 35px;
        }
    }
    
    table.group_table {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        margin: 0;
        border: none;
        padding: 0;
        
        tbody {
            display: block;
        }
        
        tr {
            display: block;
            padding: 0 0 10px;
            border: 0;
            
            &:last-child {
                margin: 0;
            }
        }
        
        td {
            padding: 0 17px 10px 0;
            text-align: left;

            &.woocommerce-grouped-product-list-item__label{
                a{
                    color: $first-main-menu-color;
                    font-size: 12px;
                    font-weight: 500;
                    letter-spacing: .1em;
                    text-transform: uppercase;
                    &:hover{
                        color: $first-main-color;
                    }
                }
            }
            
            &.price {
                display: table-cell;
                vertical-align: middle;
            }
            
            &:last-child {
                padding: 0 0 10px;
            }
            @media only screen and (max-width: $phone-landscape){
                display: inline-block;
                padding: 0 17px 15px 0;
            }
        }
        
        .mkdf-quantity-buttons {
            margin: 0;
        }
        
        a {
            color: $default-text-color;
            
            &:hover {
                color: $first-main-color;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Variation styles - end
   ========================================================================== */