/* ==========================================================================
   WooCommerce Pagination styles - begin
   ========================================================================== */
.mkdf-woo-pagination-holder {
    @include mkdfRelativeHolderLayout();
    padding: 3px 0 75px;
    clear: both;

    .mkdf-woo-pagination-inner {
        position: relative;
        display: table;
        width: 100%;
        text-align: right;
    }
}

.woocommerce-pagination {
    position: relative;
    display: inline-block;
    vertical-align: top;
    width: 100%;
    margin: 0;
    padding: 3px 0 75px;
    text-align: center;

    .page-numbers {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        margin: 0;
        padding: 0;

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 0;

            a,
            span {
                position: relative;
                display: inline-block;
	            width: auto;
                color: #595858;
	            font-size: 12px;
                margin: 0 3px;
                padding: 0;
                vertical-align: top;
                text-decoration: none;
                cursor: pointer;
                white-space: nowrap;
                @include mkdfTransition(color .2s ease-out);

                &:hover{
                    color: $first-main-color;
                }
                &.current {
                    color: $meta-color;
                }
            }

            a {

                &.next,
                &.prev {
                    font-size: 0;
                    line-height: 1.2;
                    vertical-align: middle;
                    margin: 0 6px 0 4px;

                    &:before {
                        position: relative;
                        top: -1px;
                        display: block;
                        font-family: 'Linearicons-Free';
                        font-size: 15px;
                        line-height: inherit;
                        -webkit-font-smoothing: antialiased;
                        -moz-osx-font-smoothing: grayscale;
                    }
                }
    
                &.prev {
        
                    &:before {
                        content: "\e875";
                    }
                }
    
                &.next {
        
                    &:before {
                        content: "\e876";
                    }
                }
            }
        }
    }
}

.mkdf-woo-view-all-pagination {
    position: relative;
    display: table-cell;
    vertical-align: top;
    width: 1%;

    a {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        white-space: nowrap;
        padding: 0 0 0 17px;
        color: $default-text-color;

        &:before {
            content: '';
            padding: 0 28px 0 0;
            border-left: 1px solid $default-text-color;
        }

        &:hover {
            color: $first-main-color;
        }
    }
}
/* ==========================================================================
   WooCommerce Pagination styles - end
   ========================================================================== */