/* ==========================================================================
   WooCommerce Cart page styles - begin
   ========================================================================== */

.mkdf-woocommerce-page {
	
	table.cart {
		
		thead {
			
			tr {
				border: 0;
				border-bottom: 1px solid $default-border-color;
				
				th {
					padding: 10px 0;
					color: $default-heading-color;
					text-align: left;
					font-size: 14px;
					font-weight: 500;
					letter-spacing: 0.1em;
					text-transform: uppercase;
				}
			}
		}
		
		tbody {
			
			tr {
				border: 0;
			}
		}
		
		tr.cart_item {
			border-bottom: 1px solid $default-border-color;
			
			td {
				padding: 25px 0;
				vertical-align: middle;
				text-align: inherit;

				&.product-name {
					font-size: 12px;
					letter-spacing: .2em;
					text-transform: uppercase;
					padding-right: 30px;
					@media only screen and (max-width: $phone-landscape){
						padding-right: 0;
					}
				}
				&.product-remove {
					padding-left: 10px;
					padding-right: 10px;
					
					a {
						font-size: 0;
						line-height: 1;
						color: $default-text-color;
						
						&:after {
							content: "\f404";
							display: inline-block;
							vertical-align: middle;
							speak: none;
							color: inherit;
							font-family: "Ionicons";
							font-size: 25px;
							line-height: inherit;
							font-style: normal;
							font-weight: normal;
							font-variant: normal;
							text-transform: none;
							text-rendering: auto;
							-webkit-font-smoothing: antialiased;
							-moz-osx-font-smoothing: grayscale;
						}
						
						&:hover {
							color: $default-heading-color;
						}
					}
				}
				
				&.product-thumbnail {
					width: 86px;
					padding-right: 10px;
					
					a, img {
						display: block;
					}
				}
				
				.variation {
					
					> * {
						display: inline-block;
						vertical-align: middle;
						padding: 0;
						margin: 0;
					}
					
					p {
						margin: 0;
					}
				}
			}
		}
		
		td.actions {
			padding: 35px 0 36px;
			text-align: inherit;
			
			.coupon {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				float: left;
				
				label {
					display: none;
				}
				
				input[type="text"] {
					float: left;
				    font-size: 12px;
				    line-height: 20px;
				    font-weight: inherit;
				    color: $default-form-color;
				    background-color: transparent;
				    border: 1px solid $default-border-color;
				    border-radius: 0;
				    outline: 0;
				    -webkit-appearance: none;
				    cursor: pointer;
				    box-sizing: border-box;
				    @include mkdfTransition(border-color 0.2s ease-in-out);

				    &:focus {
				        color: $default-heading-color;
				        border-color: $default-text-color;
				    }

				    @include mkdfPlaceholder {
				        color: inherit;
				    }
					margin: 0;
					padding: 14px 20px;
				}
				.button{
					float: left;
					background-color: $default-heading-color;
				}
			}
			
			> input, button {
				float: right;
			}
		}
	}
	
	.cart-collaterals {
		@include mkdfRelativeHolderLayout;
		margin: 0 0 40px;
		
		h2 {
			position: relative;
			display: block;
			margin: 0 0 26px;
		}
		
		table {
			margin: 0 0 35px;
			
			tr {
				border: 0;
				border-bottom: 1px solid $default-border-color;
			}
			
			td, th {
				padding: 14px 0;
				text-align: left;
				vertical-align: middle;
			}
			
			th {
				width: 35%;
				padding-right: 5px;
				border: 0;
				color: $default-heading-color;
				font-size: 14px;
				font-weight: 500;
				letter-spacing: 0.1em;
				text-transform: uppercase;
			}
		}
		
		tr.shipping {
			
			#shipping_method {
				list-style: none;
				margin: 0 0 7px;
				
				li {
					margin: 3px 0;
					padding: 0;
					
					> * {
						display: inline-block;
						vertical-align: middle;
						margin: 0;
					}
				}
			}
		}
		
		.woocommerce-shipping-calculator {
			
			.shipping-calculator-button {
				display: block;
				margin: 0 0 10px;
			}
			
			p {
				margin: 0;
			}
		}
	}
	
	.cross-sells {
		margin: 30px 0 0;
		border-top: 1px solid $default-border-color;
		
		> h2 {
			display: block;
			margin: 36px 0 50px;
			text-align: center;
		}
	}
	
	.cart_totals {
		margin: 1px 0 20px;
	}
}

.mkdf-woocommerce-page {
	
	.cart-empty {
		position: relative;
		display: block;
		font-size: 25px;
		line-height: 1.5em;
		text-align: center;
		color: $default-heading-color;
		margin: 40px 0 33px;
	}
	
	.return-to-shop {
		position: relative;
		display: block;
		text-align: center;
		margin: 0 0 60px;
	}
	&.woocommerce-cart {
		.woocommerce {
			margin: 20px 0 20px;
		}
	}
}
/* ==========================================================================
   WooCommerce Cart page styles - end
   ========================================================================== */