/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.mkdf-pl-holder {
    @include mkdfRelativeHolderLayout();
	
	&.mkdf-info-on-image {
		
		.mkdf-pli {
			
			.mkdf-pli-add-to-cart {
				margin: 25px 0 0;
			}
			.mkdf-pli-title{
				margin: 0 0 12px;
			}
		}
	}


	&.mkdf-info-below-image .mkdf-pli-inner:hover{
		.mkdf-pli-image img{
			@include mkdfTransform(scale(1.03) rotate(0.01deg));
		}
	}

    .mkdf-pli {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        padding: 0;
        z-index: 100;
        box-sizing: border-box;
        &:hover {
            .mkdf-pli-text-wrapper {
            	.mkdf-pli-add-to-cart {
            		 opacity: 1;
                	 @include mkdfTransform(translateX(-50%));
            	}
            	.mkdf-pli-price {
            		 opacity: 0;
                    @include mkdfTransform(translateX(60px));
            	}
               
            }
        }

	    .mkdf-pli-title {
		    margin: 0 0 5px;
		    line-height: 1;
	    }
	
	    .mkdf-pli-category {
		    margin: 11px 0 3px;
	    }

        .mkdf-pli-excerpt {
            color: $default-heading-color;
	        line-height: 1.3em;
            margin: 11px 0 3px;
        }

	    .mkdf-pli-rating-holder {
		    @include mkdfRelativeHolderLayout();
		    line-height: 1;
		    margin: 0px 0 13px;
	    }

	    .mkdf-pli-rating {
		    @include mkdfWooCommerceRatingsStyle();
	    }

        .mkdf-pli-price {
	        @include mkdfWooCommercePriceStyle();
	        @include mkdfTransition(all .3s ease);
	        font-size: 12px;
        }
    }

    .mkdf-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

		&:hover {

            .mkdf-pli-text.mkdf-pli-hover-overlay {
                opacity: 1;
            }

            .mkdf-pli-text.mkdf-pli-button-from-bottom .mkdf-button,
            .mkdf-pli-text.mkdf-pli-button-from-bottom .added_to_cart{
	            @include mkdfTransform(translateY(0));
		    }
        }


        .touch & {
            cursor: pointer;
        }

        .mkdf-pli-image {
            @include mkdfRelativeHolderLayout();
			overflow: hidden;

            img {
                display: block;
                width: 100%;
				@include mkdfTransform(scale(1) rotate(0.01deg));
				@include mkdfTransitionTransform($default-transition-duration $default-easing-function);
            }

            .mkdf-pli-onsale,
            .mkdf-pli-out-of-stock,
            .mkdf-pli-new-product {
	            @include mkdfWooCommerceMarksGeneralStyle();
            }

            .mkdf-pli-onsale {
	            @include mkdfWooCommerceSaleStyle();
            }

            .mkdf-pli-out-of-stock {
	            @include mkdfWooCommerceOutOfStockStyle();
            }
	
	        .mkdf-pli-new-product {
		        @include mkdfWooCommerceNewProductStyle();
	        }
        }
            
        .mkdf-pli-text {
            @include mkdfAbsoluteHolderLayout();
            box-sizing: border-box;


            .mkdf-button,
        	.added_to_cart{
        		white-space: normal;
        	}

            &.mkdf-pli-hover-overlay{
            	padding: 10px 15px;
	            opacity: 0;
	            background-color: rgba($default-background-color, 0.9);
                @include mkdfDefaultTransition(opacity);
                .mkdf-pli-price {
            		display: block;
            		color: #afafaf;
 
            	}

            }

            &.mkdf-pli-button-from-bottom{

            	.mkdf-pli-text-inner{
            		vertical-align: bottom;
            	}

            	.mkdf-button,
            	.added_to_cart{
            		width: 100%;
            		padding-left: 10px;
            		padding-right: 10px;
            		@include mkdfTransform(translateY(100%));
            		@include mkdfTransitionTransform($default-transition-duration $default-easing-function);
            	}
            }
        }

        .mkdf-pli-text-outer {
	        @include mkdfTableLayout();
	        text-align: center;
        }

        .mkdf-pli-text-inner {
            @include mkdfTableCellLayout();

            .mkdf-pli-category a,
            .mkdf-pli-excerpt,
            .mkdf-pli-rating,
            .mkdf-pli-price {
            	display: block;
                color: $default-heading-color;
            }

            .mkdf-pli-price del {
		        color: $meta-color;
            }
            .mkdf-pli-price ins {
		        color: $first-main-menu-color;
            }
	
	        .mkdf-pli-category a:hover {
		        color: rgba(#fff, .8);
	        }

	        .mkdf-pli-add-to-cart {
				display: inline-block;
		        &.mkdf-default-skin {

			        .button,
			        .added_to_cart {
				        @include mkdfButtonSolidColor();

				        &:hover {
				            @include mkdfButtonSolidHoverColor();
				        }
			        }
		        }

		        &.mkdf-light-skin {

			        .button,
			        .added_to_cart {
				        color: $default-heading-color;
				        background-color: #fff;

				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
		
		        &.mkdf-dark-skin {
			
			        .button,
			        .added_to_cart {
				        color: #fff;
				        background-color: $default-heading-color;
				
				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
	        }

            .button,
            .added_to_cart {
	            @include mkdfButtonDefaultStyle();
	            @include mkdfButtonMediumParams();
	            white-space: nowrap;
	            z-index: 3;
            }

            .button.added {
                display: none;
            }
        }

	    .mkdf-pli-link {
		    @include mkdfAbsoluteHolderLayout();
	    }
    }

    .mkdf-pli-text-wrapper {
        @include mkdfRelativeHolderLayout();
        margin: 23px 0 0;
        text-align: center;

        .mkdf-pli-add-to-cart{
			display: inline-block;
			position: absolute;
            left: 50%;
            opacity: 0;
            font-size: 12px;
            @include mkdfTransform(translateX(-100%));
            @include mkdfTransition(all .4s ease);
            a {
            	color: #afafaf;
				&:hover {
					color: $first-main-color;
				}
            }

			.button{
				&.added{
					display: none;
				}
			}
		}
		
		.mkdf-pli-price {
			display: inline-block;
		}
    }
	
	&.mkdf-product-info-light {
		
		.mkdf-pli-inner {
			
			.mkdf-pli-text-inner {
				.mkdf-pli-title,
				.mkdf-pli-category a,
				.mkdf-pli-excerpt,
				.mkdf-pli-rating,
				.mkdf-pli-price {
					color: #fff;
				}
				
				.mkdf-pli-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}
	
	&.mkdf-product-info-dark {
		
		.mkdf-pli-inner {
			
			.mkdf-pli-text-inner {
				
				.mkdf-pli-title,
				.mkdf-pli-category a,
				.mkdf-pli-excerpt,
				.mkdf-pli-rating,
				.mkdf-pli-price {
					color: $default-heading-color;
				}
				
				.mkdf-pli-category a:hover {
					color: rgba($default-heading-color, .8);
				}
			}
		}
	}
	
	/*************** Standard Layout - begin ***************/
	
	&.mkdf-standard-layout {
		
		.mkdf-pli {
			float: left;
		}
		
		&.mkdf-large-space {
			margin: 0 0 -50px;
		}
		
		&.mkdf-normal-space {
			margin: 0 0 -30px;
		}
		
		&.mkdf-small-space {
			margin: 0 0 -20px;
		}
		
		&.mkdf-tiny-space {
			margin: 0 0 -10px;
		}
		
		$standard_list_columns: ('six');
		
		@for $i from 0 to length($standard_list_columns) {
			&.mkdf-#{nth($standard_list_columns,$i+1)}-columns {
				$column_width: 100%/6;
				
				.mkdf-pli {
					width: $column_width;
					
					@media only screen and (min-width: $laptop-landscape-large-plus-pixel) {
						
						&:nth-child(#{$i+6}n+1) {
							clear: both;
						}
					}
				}
			}
		}

		$standard_list_columns: ('five');

		@for $i from 0 to length($standard_list_columns) {
			&.mkdf-#{nth($standard_list_columns,$i+1)}-columns {
				$column_width: 100%/5;

				.mkdf-pli {
					width: $column_width;

					@media only screen and (min-width: $laptop-landscape-mac-plus-pixel) {

						&:nth-child(#{$i+5}n+1) {
							clear: both;
						}
					}
				}
			}
		}

		$standard_list_columns: ('two', 'three', 'four');

		@for $i from 0 to length($standard_list_columns) {
			&.mkdf-#{nth($standard_list_columns,$i+1)}-columns {
				$column_width: 100%/($i+2);

				.mkdf-pli {
					width: $column_width;

					@media only screen and (min-width: $ipad-landscape-plus-pixel) {

						&:nth-child(#{$i+2}n+1) {
							clear: both;
						}
					}
				}
			}
		}
	}
	
	/*************** Standard Layout - end ***************/
	
	/*************** Masonry Layout - begin ***************/
	
	&.mkdf-masonry-layout {
		
		.mkdf-pl-outer {
			opacity: 0;
		}
		
		.mkdf-pl-sizer {
			width: 100%;
		}
		
		.mkdf-pl-gutter {
			width: 0;
		}
		
		.mkdf-pli-inner {
			width: 100%;
			height: 100%;
			
			.mkdf-pli-image {
				height: 100%;
				
				img {
					height: 100%;
				}
			}
		}
		
		.mkdf-pli.mkdf-woo-image-large-width {
			
			img {
				width: 100%;
			}
		}

		$masonry_list_columns: ('two', 'three', 'four', 'five', 'six');
		
		@for $i from 0 to length($masonry_list_columns) {
			&.mkdf-#{nth($masonry_list_columns,$i+1)}-columns {
				$column_width: 100%/($i+2);
				
				.mkdf-pli,
				.mkdf-pl-sizer {
					width: $column_width;
				}
				
				.mkdf-pli.mkdf-woo-image-large-width,
				.mkdf-pli.mkdf-woo-image-large-width-height {
					width: $column_width*2;
				}
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/

	/*************** Filter - start ***************/

	.mkdf-pl-cat-order-holder{
		font-size: 0;
		margin-bottom: 27px;
	}

	.mkdf-pl-categories{
		float: left;
		width: 80%;
		text-align: left;

		h6.mkdf-pl-categories-label {
			margin: 0;
			display: none;
			cursor: pointer;

			&:after {
				content: "\43";
				position: relative;
				display: inline-block;
				vertical-align: middle;
				font-family: "ElegantIcons";
				font-size: 20px;
				line-height: 1em;
				margin-left: 8px;
				top: -1px;
			}
		}

		ul{
			list-style-type: none;
			margin-bottom: 0;

			li{
				display: inline-block;
				vertical-align: middle;
			    font-size: 12px;
				letter-spacing: .1em;
				font-weight: 500;
				text-transform: uppercase;

				&:not(:last-child) {
					margin-right: 32px;
				}

				a {
					color: #1b1b1b;

					&.active,
					&:hover {
						color: $first-main-color;
					}
				}
				
			}
		}
	}
	
	.mkdf-pl-ordering-outer{
		position: relative;
		float: right;
		width: 20%;
		text-align: right;

		.mkdf-pl-ordering-holder{
			display: inline-block;
			vertical-align: middle;

			&:hover{
				.mkdf-pl-ordering {
					opacity: 1;
					z-index: 150;
				}
			}

			.mkdf-pl-ordering-filter-title{
				cursor: pointer;
				margin: 15px 0 0;

				&:after{
					content: '\43';
					display: inline-block;
					vertical-align: top;
					font-family: 'ElegantIcons';
					font-size: 20px;
					padding-left: 10px;
				}
			}
		}
		
		.mkdf-pl-ordering{
			position: absolute;
			opacity: 0;
			right: -8px;
			color: #737373;
			background-color: #fff;
			text-align: left;
			padding: 13px 30px 17px;
			width: 140%;

			.mkdf-pl-ordering-inner{
				display: inline-block;
				vertical-align: top;
				min-width: 150px;
			}

			ul{
				list-style-type: none;
			}

			a{
				font-size: 13px;
				text-transform: capitalize;
				font-weight: 500;
				color: inherit;

				&.active,
				&:hover{
					color: #000;
				}
			}
		}
	}

	.mkdf-prl-loading{
		position: absolute;
		top: 0;
		left: 0;
		display: none;
		width: 100%;
		height: 100%;
		text-align: center;
		background-color: #fff;
		z-index: 200;

		.mkdf-prl-loading-msg{
			display: inline-block;
			vertical-align: middle;
			margin-top: 200px;
		}
	}

	/*************** Filter - end ***************/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */