/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.mkdf-woo-single-page {

	&.mkdf-woo-single-thumb-on-left-side {

		.mkdf-single-product-content {

			.images {

				@media only screen and (min-width: $ipad-portrait-plus-pixel) {

					figure {
						width: 100%;
						margin: 0;
					}

					.woocommerce-product-gallery__image {

						&:first-child {
							left: 148px;
							width: calc(100% - 187px);
						}
					}

					&.woocommerce-product-gallery--with-images {

						.woocommerce-product-gallery__image {

							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: 118px;
								height: calc(25% + 5px);
								padding: 0 0 20px;
								box-sizing: border-box;

								a {
									height: 100%;

									img {
										height: 100%;
									}
								}
							}

							$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
							@for $i from 0 to length($thumbnail_numbers) {
								$thumbnail_number_value: nth($thumbnail_numbers, $i+1);

								&:nth-child(#{$thumbnail_number_value}#{n}) {
									$first_param: (25 * $i)#{'%'};
									$second_param: (5 * $i)#{px};
									$topPos: calc(#{$first_param} + #{$second_param});

									top: $topPos;
								}
							}
						}
					}
				}
			}
		}
	}

	.product {
		@include mkdfRelativeHolderLayout();
		float: none;
		margin: 17px 0 28px;

		&.product-type-external {
			.mkdf-single-product-summary {
				.cart {
					margin-top: 7px;
				}
			}
		}
	}
	.woocommerce-grouped-product-list-item {
		margin: 0;
	}
	.mkdf-single-product-content {
		@include mkdfRelativeHolderLayout();
		clear: both;
		margin: 0 0 105px;

		.images {
			position: relative;
			display: inline-block;
			vertical-align: middle;
			width: 55%;
			margin: 0;
			padding: 0 30px 0 0;
			float: left;
			box-sizing: border-box;

			figure {
				position: relative;
				display: inline-block;
				vertical-align: top;
				margin: 0 -10px;

				a {
					@include mkdfRelativeHolderLayout();

					img {
						position: relative;
						width: 100%;
						display: block;
					}
				}
			}

			.woocommerce-product-gallery__trigger {
				position: absolute;
				top: 10px;
				right: 39px;
				width: 30px;
				height: 30px;
				line-height: 30px;
				text-align: center;
				z-index: 10;
			}

			.woocommerce-product-gallery__image {
				position: relative;
				display: inline-block;
				vertical-align: top;
				margin: 0;
				padding: 0 10px;
				box-sizing: border-box;

				&:first-child {
					left: 10px;
					width: calc(100% - 20px);
					padding: 0;
				}

				.zoomImg {
					cursor: crosshair;
				}
			}

			&.woocommerce-product-gallery--with-images {

				.woocommerce-product-gallery__image {

					&:not(:first-child) {
						float: left;
						padding-top: 20px;
					}
				}
			}

			$thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
			@for $i from 0 to length($thumb_3_columns_label) {
				&.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
					$column_width: 100%/($i+2);

					.woocommerce-product-gallery__image {

						&:not(:first-child) {
							width: $column_width;
						}
					}
				}
			}
		}
	}

	.select2-container--default {
		
		&.select2-container--open {

			.select2-dropdown {

				left: 21px;
				top: 6px;

			}

		}
	}

	.mkdf-single-product-summary {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		width: 45%;
		float: left;
		box-sizing: border-box;

		.mkdf-single-product-title {
			display: block;
			margin: 0;
		}

		.price {
			@include mkdfWooCommercePriceStyle();

			del {
				font-size: 15px;
			}

			ins {
				font-size: 18px;
				font-weight: 400;
				color: #080808;
			}
		}

		.woocommerce-product-rating {
			@include mkdfRelativeHolderLayout();
			line-height: 1;
			margin: 3px 0 43px;

			.star-rating {
				color: #ffd117;
			}

			.woocommerce-review-link {
				font-size: 12px;
				color: #868686;
				text-transform: uppercase;
			}
		}

		.woocommerce-product-details__short-description {
			@include mkdfRelativeHolderLayout();
			margin: 23px 0 32px;

			p {

				margin: 0;

			}
		}

		.product_meta {
			margin: 32px 0 0;

			> span {
				display: block;
				margin: 0 0 7px;
				color: $default-heading-color;
				font-weight: 500;
				font-size: 12px;

				span, a {
					color: #8b8b8b;
					font-size: 12px;
					font-weight: 500;
				}

				a {
					color: $default-text-color;

					&:hover {
						color: $first-main-color;
					}
				}
			}
		}

		.select2-container--default .select2-selection--single {
			.select2-selection__rendered {
				color: #999999;
				text-transform: capitalize;
				padding: 0 0 0 8px;
			}
			.select2-selection__arrow {
				font-size: 16px;
				right: 9px;
				color: #999999;
				&:hover {
					color: $first-main-color;
				}
			}

		}


		.mkdf-woo-social-share-holder {
			@include mkdfRelativeHolderLayout();
			margin: 10px 0 0;

			> * {
				display: table-cell;
				vertical-align: top;
			}
			> span {
				padding: 0 9px 0 0;
				color: $default-heading-color;
				font-weight: 700;
				font-size: 13px;
				letter-spacing: 0.05em;
				text-transform: uppercase;
			}
		}

		p.stock.out-of-stock,
		p.stock.in-stock {
			color: $default-heading-color;
			font-weight: bold;
			margin: 18px 0 0;
		}

		form.cart {
			@include mkdfRelativeHolderLayout();
		}

		.button[type=submit].single_add_to_cart_button {
			background-color: #080808;
			padding: 14px 42px;
			&:hover {
				background-color: #232323;
			}
		}

	}

	.woocommerce-tabs {
		@include mkdfRelativeHolderLayout();
		margin: 14px 0 0;

		ul.tabs {
			@include mkdfRelativeHolderLayout();
			display: block;
			list-style: none;
			margin: 0;
			padding: 0;
			font-size: 0;
			text-align: left;
			clear: both;

			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				border: 1px solid #eee;
				margin-bottom: -1px;
				margin-left: -1px;
				&.active {
					background-color: $first-main-color;
					a {
						color: #ffffff;
					}
				}

				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 15px 47px;
					@include mkdfH6Style();
				}
			}
		}

		.entry-content {
			margin: 0 -310px;
			padding: 73px 310px 80px;
			border-top: 1px solid #eee;
			border-bottom: 1px solid #eee;
			@include mkdfRelativeHolderLayout();

			> h2 {
				display: none;
			}

			> p {
				margin: 20px 0;
			}
		}
		.mkdf-st-text {

		}

		table {
			margin: 5px 15px 0;

			tr, th {
				border: 0;
			}

			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;

				p {
					margin: 0;
				}
			}

			th {
				color: $default-heading-color;
				width: 1%;
				padding: 0 10px 0 0;
				font-size: 12px;

				&:after {
					content: ":";
				}
			}

			td {
				width: 99%;
				font-size: 12px;
				p{
					font-family: $default-nav-font;
				}
			}
		}

		#reviews {
			@include mkdfRelativeHolderLayout();
			padding: 3px 15px 10px;
			box-sizing: border-box;

			input[type=email] {
				margin: 0;
			}

			h2 {
				font-size: 30px;
				line-height: 24px;
				margin: 3px 0 60px;
			}

			ol.commentlist {
				list-style: none;

				li {
					margin: 0 0 23px;

					&:last-child {
						margin: 0;
					}
				}

				.comment_container {
					@include mkdfRelativeHolderLayout();

					img {
						float: left;
						width: 70px;
						border-radius: 4em;
					}
				}

				.comment-text {
					padding: 0 0 0 90px;

					p {
						margin: 0;
					}
					.meta {
						font-family: $default-nav-font;
						@include mkdfH6Style();
						line-height: 15px;
						padding-bottom: 17px;

						.woocommerce-review__published-date {
							display: inline-block;
							font-size: 10px;
							color: #afafaf;
							letter-spacing: .15em;
							margin: 0 0 0 3px;
						}

						strong {
							color: $first-main-menu-color;
							font-weight: 500;
							vertical-align: top;
						}

						.woocommerce-review__dash {
							display: none;
						}
					}
					.description {
						p {
							margin: 0 0 12px;
						}
					}
				}
			}

			.comment-respond {
				margin: 36px 0 0;

				.comment-reply-title {
					display: inline-block;
					font-family: $default-heading-font;
					font-size: 30px;
					line-height: 1.4em;
					margin: 0 0 12px;
					font-weight: 400;
					color: $default-heading-color;
				}

				p {
					margin: 0;

					label {
						display: block;
						padding: 0 0 5px;
					}
				}
				p.form-submit {
					margin-top: 20px;
				}

				.stars {
					overflow: hidden;

					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include mkdfWooCommerceSingleProductRatingsStyle();
					}
				}
			}
		}
	}

	.related.products,
	.upsells.products {
		margin: 30px 0 0;

		> h2 {
			display: block;
			margin: 87px 0 24px;
			font-family: $default-nav-font;
			font-size: 12px;
			text-align: left;
			font-weight: 500;
			letter-spacing: 0.1em;
			text-transform: uppercase;
		}

		.product {

			.mkdf-pl-text-wrapper {
				margin-bottom: 0;
			}
			.star-rating {
				color: #ffd117;
			}
		}
	}
	.outofstock {
		.mkdf-single-product-summary {
			.product_meta {
				margin: 0;
			}

		}

	}
}

/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */