/* ==========================================================================
Product Categories shortcode styles - begin
========================================================================== */

.mkdf-prod-cats-holder{

    .mkdf-prod-cat{
        float: left;
        position: relative;
        box-sizing: border-box;
        @include mkdfTransition(opacity .2 ease-in-out);

        .mkdf-prod-cat-inner{
            @include mkdfRelativeHolderLayout();
            @include mkdfTransition(padding 0.2s ease-in-out);
            height: 100%;
            box-sizing: border-box;
            padding: 0;
        }

        .mkdf-prod-cat-content{
           &.top{
               position: absolute;
               top:0;
               left:0;
           }

           &.bottom{
               position: relative;
               margin: 24px 0 0 0;
               text-align: center;

               .mkdf-prod-cat-button-holder{
                   margin: 32px 0 0;
               }
            }

            .mkdf-category-title{
                line-height: 1em;
                margin: 0 0 5px;
                text-align: center;
                color: #1b1b1b;
            }

            .mkdf-prod-cat-price-holder{
                text-transform: lowercase;
                font-size: 12px;
                font-weight: 400;
                color: $meta-color;
            }
        }

        &:hover{
            .mkdf-prod-cat-content{
                .mkdf-prod-cat-button-holder{
                    opacity: 1;
                }
                &.top{
                    .mkdf-prod-cat-button-holder{
                        background-color: #f4f4f4;
                    }
                }
            }
        }

        &.mkdf-cat-with-image{
        	text-align: center;

        	.mkdf-prod-cat-inner{
        		text-align: left;
        	}

        	img{
        		display: block;
        	}

            .mkdf-prod-cat-content{
                &.top{
                    top: 23px;
                    left: 25px;
                    .mkdf-prod-cat-price-holder {
                        color: #1b1b1b;
                    }
                }
            }
        }
    }

    &.mkdf-zoom {
        .mkdf-cat-with-image {
            .mkdf-pcw-inner {
                overflow:hidden;
            }

            a {
                display:block;
            }

            img {
                display:block;
				@include mkdfTransform(scale(1) translateZ(0));
				@include mkdfTransitionTransform($default-transition-duration $default-easing-function);
            }

            .mkdf-prod-cat-inner:hover { 
                .mkdf-pcw-inner {
                    @include mkdfTransform(none);

                    img{
                        @include mkdfTransform(scale(1.03));
                    }
                }
            }
        }
    }

    &.mkdf-shrink {
        .mkdf-cat-with-image {

        	.mkdf-pcw-inner{
        		position: relative;

				img {
					-webkit-clip-path: inset(0px 0px 0px 0px);
					clip-path: inset(0px 0px 0px 0px);
	         		@include mkdfTransition(all 1.25s cubic-bezier(0.01, 0.71, 0.26, 0.94));
				}
        	}

            .mkdf-prod-cat-inner:hover {
            	.mkdf-pcw-inner{
   					img {
   						-webkit-clip-path: inset(15px 15px 15px 15px);
   						clip-path: inset(15px 15px 15px 15px);
   					}
            	}

   				.mkdf-ms-explorer & {
	   				.mkdf-pcw-inner {
	   					@include mkdfTransform(scale(.94));
   					}
   				}
            }
        }
    }
}

$columns_space_label: ('no', 'tiny', 'small', 'normal', 'large');
$columns_space_width: (0, 5, 10, 15, 25);

@for $i from 0 to length($columns_space_label) {
&.mkdf-#{nth($columns_space_label,$i+1)}-space {
    $column_width: nth($columns_space_width,$i+1);
    $column_width_padding: $column_width * 2;

    .mkdf-prod-cat-holder-inner {
        @if($column_width == 0) {
            margin: 0;
            } @else {
                margin: 0 -#{$column_width}px;
            }

            .mkdf-prod-cat{
                padding: 0 #{$column_width}px;
                margin: 0 0 #{2*$column_width}px;
            }
        }
    }
}

/*************** Standard Layout - begin ***************/

$standard_list_columns: ('two', 'three', 'four', 'five', 'six');

@for $i from 0 to length($standard_list_columns) {
    &.mkdf-#{nth($standard_list_columns,$i+1)}-columns {
        $column_width: 100%/($i+2);

        .mkdf-prod-cat{
            width: $column_width;

            @media only screen and (min-width: $phone-landscape-plus-pixel) {

                &:nth-child(#{$i+2}n+1) {
                    clear: both;
                }
            }
        }
    }
}

/*************** Standard Layout - end ***************/
